
.about .about-text{
    width: 95%;
    margin: 60px auto;
    display: flex;
    flex-direction: column;
    gap:40px;
}
.about-text div{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.about-text h2{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: bolder;

}
.about-text p{
    font-family: 'Times New Roman', Times, serif;
    font-size: 17px;
    color:rgb(63, 62, 62);
}