@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;1,200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,400;1,500&display=swap');

.hero{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top:0px;
    z-index: -1;
}
.bg-img{
    width: 100%;
    height: 100%;
    position: relative;
}
.hero-content{
    position: absolute;
    top:55%;
    left: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: center;
    color: white;

}
.hero-content h1{
    letter-spacing: 1.5px;
    font-size: 40px;
    font-family: 'Roboto',sans-serif;
    font-weight: bolder
}
.hero-content p{
    font-size: 20px;
    font-weight: 100;
    font-family: Arial, Helvetica, sans-serif;
}
.hero-content .hero-btn{
    background-color: white;
    width: 150px;
    display: block;
    margin: 5px auto;
    border: none;
    height: 40px;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: 1.4px;
    border-radius: 10px;
    cursor: pointer;
    text-align: center;


}
.null{
    display:none;
}
.bg-img img{
    width: 100%;
    height: 100%;
    filter: brightness(0.4);
    filter: blur(0.4);
    object-fit: cover;
}

@media screen and (max-width:1080px) {
    .hero-content h1{
        font-size: 30px;
        font-family: 'Poppins', sans-serif;
        text-align:  center;
        font-weight: bolder;
    }
    .hero-content p{
        font-size: 18px;
        font-family: 'Poppins', sans-serif;
        color: rgb(233, 230, 230);
    }
    .hero-content{
        width: 90%;
        margin: 0px auto;

    }
    .bg-img img{
        object-fit: cover;
    }
    
}