.footer{
    background-color: black;
    padding: 20px;
    width: 100%;
    height:100%;
    margin-top:100px ;
    color: white;
    padding: 40px 30px;
    position:relative;
    top: 0px;


}
.footer-container{
    margin: 0px auto;
    width: 90%;
}
.upper-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
@media screen and (max-width:1080px) {
    .upper-footer{
        flex-direction: column;
        gap: 20px;
        align-items: flex-start;
    }
    
}
.upper-footer>div:nth-of-type(1) p{
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 100;
    margin-top: 10px;

}
.social-icons{
    display: flex;
    gap: 15px;
}
.lower-footer{
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(4,1fr);
}
.lower-footer h3{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 25px;
    font-weight: bolder;
}
.lower-footer div{
    display: flex;
    flex-direction: column;
    gap:10px;
    padding-top: 10px;
}
.lower-footer div h2{
    font-size: 18px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
@media screen and (max-width:1080px ) {
    .lower-footer{
        grid-template-columns: repeat(2,1fr);
        grid-gap: 50px;
    }
    .lower-footer div h2{
        font-size: 16px;

    }
    
}