.header{
    width:100%;
    height: 100vh;
    /* position: relative; */
}
nav{
    width: 95%;
    margin: 20px 30px;
    background-color:#ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    border-radius: 10px;
    position:fixed;
    box-shadow: 10px 10px 10px -3px rgb(63, 63, 63);
}
.left-nav a{
    font-size: 24px;
    letter-spacing: 1px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bolder;
    color: black;
}
.right-nav{
    display: flex;
    justify-content: space-between;
    gap: 30px;
    align-items: center;
}
.right-nav ul{
    display: flex;
    gap: 37px;
}
.right-nav ul li a{
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 16px;
    color: black;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
}
.right-nav button{
    background-color: #f2f0f2;
    font-size: 16px;
    font-weight: bold;
    text-shadow: 1px 1px -2px black;
    color: black;
    padding: 6px 10px;
    border: none;
    cursor: pointer;
}
#btns_container{
    display:none ;
    position: absolute;
    top: 20px;
    right: 20px;
    margin-top: 5px;
}
@media screen and (max-width:1080px) {
    nav{
        flex-direction: column;
        align-items: flex-start;
        width: 85%;
        height: 12vh;
        margin-left: 30px;
        overflow: hidden;

    }
    .right-nav{
        flex-direction: column;
        margin: 80px auto;
    }
    .right-nav ul {
        flex-direction: column;
        gap:40px
    }
    .right-nav ul li a{
        gap: 10px;
    }
    #btns_container{
        display: initial;
    }
    nav.active{
        height: 80vh;
        transition: 0.5s;
    }
    nav.off{
        height: 11vh;
        transition: 0.5s;
    }
    .right-nav button{
        width: 150px;
        height: 40px;
        letter-spacing: 1px;
        background-color: #028b8e;
        color: white;
    }
    
}