.form{
    margin: 40px 10px;
 
}
form{
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 70%;
    margin: 0px auto;

}
label{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 22px;
    font-weight: bold;
}
input,textarea{
    height: 50px;
    padding-left: 20px;
    outline: none;
    font-size: 20px;
}
button{
    display: block;
    width: 150px;
    text-align: center;
    letter-spacing: 1px;

}
textarea{
    padding-top: 10px;
}
.form div{
    display: flex;
    flex-direction: column;
    gap: 10px;

}