@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,400;1,500&display=swap');
.popular{
    padding: 70px 0px ;
    width: 100%;
}
.popular-header{
    text-align: center;
    margin:80px 0px
}
.popular-header h1{
    font-family: 'Roboto', sans-serif;
    font-size: 35px;

}
.popular-header p{
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 10px;

}
.left-poular h2{
    font-family: 'Roboto',sans-serif;
    font-size: 25px;
}
.left-poular p{
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 25px;
    margin-top: 10px;
    text-align: justify;
}
.popular-contaier{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 60px;
    width: 90%;
    margin: 0px auto;
}
.right-popular{
    display: flex;
    gap: 10px;
}
.right-popular img{
    width: 40%;
    height: 250px;
    border-radius: 10px;
}

@media screen and (max-width:1080px) {
    .popular-contaier{
        grid-template-columns: 1fr;
    }
    .right-popular{
        margin: -30px auto;

    }
    .right-popular img{
        width: 50%;
    }
    
}