.trips{
    width: 100%;
    background-color: #ebebeb;
}
.trips-header{
    text-align: center;
    margin:80px 0px
}
.trips-header h1{
    font-family: 'Roboto', sans-serif;
    font-size: 35px;

}
.trips-header p{
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 10px;
    font-weight: bolder

}
.trips-container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    width: 90%;
    margin: 0px auto;
    grid-gap: 50px;
}
.trip{
    border:1px solid rgba(0, 0, 0, 0.61);
    border-radius: 10px 10px 0px 0px;
}
.img-trip{
    width: 100%;
    height: 200px;
    overflow: hidden;
}
.img-trip img{
    width: 100%;
    height: 100%;
    border-radius: 10px 10px 0px 0px;
}
.img-trip img:hover{
    transform: scale(1.6);
    transition: all 0.9s;
    border-radius: 10px 10px 0px 0px;

    
}
.text-trip{
    margin: 20px 10px;
    background-color: rgb(245, 245, 245);

    padding: 0px;
}
.text-trip h2{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;
    font-weight: 600;

}
.text-trip p{
    color: #999899;
    font-size: 17px;
    line-height: 25px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 100;
    margin-top: 20px;
}
@media screen and (max-width:1080px) {
    .trips-container{
        grid-template-columns: 1fr;
    }
    
}